.section-servicios {
  padding-top: 3rem;
  padding-bottom: 3rem;
  .container {
    .title {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 2rem;
      @media (min-width: 768px) {
        margin-bottom: 3rem;
      }
      img {
        height: 2rem;
      }
      h1 {
        margin: 0 1rem;
        font-weight: bold;
        font-style: italic;
        color: $gray-dark;
        position: relative;
        z-index: 1;
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 6rem;
          height: 6rem;
          border-radius: 50%;
          background-color: $gray-light;
          z-index: -1;
        }
      }
    }
    .content-cards {
      .card-col {
        padding-top: 1rem;
        padding-bottom: 1rem;
        @media (min-width: 1200px) {
          padding-top: 3rem;
          padding-bottom: 3rem;
          &:nth-child(2n+1) {
            padding-right: 3rem;
          }
          &:nth-child(2n) {
            padding-left: 3rem;
          }
        }
        .item-card {
          position: relative;
          width: 100%;
          padding-bottom: 60%;
          border: none;
          border-radius: 15px;
          border-bottom-left-radius: 30px;
          @media (min-width: 1200px) {
            border-bottom-left-radius: 50px;
          }
          &:hover {
            .img-card {
              transform: scale(1.05);
            }
          }
          .img-card {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border: none;
            border-radius: 15px;
            object-fit: cover;
            object-position: center;
            border-bottom-left-radius: 30px;
            transition: all 0.3s;
            @media (min-width: 1200px) {
              border-bottom-left-radius: 50px;
            }
          }
          .btn-group-custom {
            position: absolute;
            bottom: 1rem;
            left: 1rem;
            border: none;
            border-radius: 50px;
            border-top-left-radius: 0px;
            padding: 1rem 0;
            font-size: 1.5rem;
            transition: all 0.3s;
            .btn-text {
              padding: 0 6rem 0 2rem;
              color: $text-gray;
              transition: all 0.3s;
            }
            .btn-icon {
              padding: 0 1.5rem;
              background-color: $celeste;
              border-bottom-right-radius: 50px;
              border-top-right-radius: 50px;
              height: 100%;
              position: absolute;
              right: 0;
              top: 0;
              display: flex;
              align-items: center;
              color: $white;
              transition: all 0.3s;
            }
            &:hover {
              background-color: $celeste;
              box-shadow: 0 0 5px $white;
              .btn-text {
                color: $white;
              }
              .btn-icon {
                background-color: $white;
                color: $celeste;
              }
            }
          }
        }
      }
    }
  }
}