.section-perfil {
  padding-top: 3rem;
  padding-bottom: 3rem;
  .col-izq {
    @media (max-width: 767.98px) {
      box-shadow: 2px 52px 19px -56px rgba(0,0,0,0.75);
      padding-bottom: 3rem;
    }
    .title-black {
      color: $gray-dark;
      font-weight: 700;
      text-align: center;
      letter-spacing: 2px;
      margin-bottom: 0;
    }
    .subtitle-celeste {
      color: $celeste;
      text-align: center;
      letter-spacing: 3px;
      font-size: 1.1rem;
    }
    .content-icons {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 3rem;
    }
    .enlace-perfil {
      text-decoration: none;
      color: $text-gray;
      transition: all .3s;
      &:hover {
        color: $celeste;
      }
    }
  }
  .col-der {
    text-align: center;
    @media (min-width: 768px) {
      box-shadow: -35px 0px 16px -41px rgba(0,0,0,0.75);
    }
    .img-google {
      width: 40%;
      margin: 0 auto;
    }
    .btn-google {
      position: relative;
      display: inline-block;
      border: none;
      border-radius: 30px;
      border-top-left-radius: 0px;
      border-bottom-right-radius: 0px;
      padding: 1rem 3rem;
      background-color: $gray-dark;
      transition: all 0.3s;
      &:hover {
        background-color: $celeste;
      }
      a {
        color: $white;
        text-decoration: none;
      }
      &:after {
        content: '';
        position: absolute;
        background-image: url(../images/banderin-btn.png);
        background-size: contain;
        background-position: top center;
        background-repeat: no-repeat;
        width: 2rem;
        height: 2rem;
        top: 0px;
        right: 20px;
        transform: translateY(-15%);
      }
    }
  }
}