.section-contacto {
  background: $black;
  @media (min-width: 768px) {
    background: linear-gradient(90deg, rgba(102, 102, 102, 1) 0%, rgba(0, 0, 0, 1) 50%, rgba(0, 0, 0, 1) 100%);
  }
  .container {
    padding-top: 3rem;
    padding-bottom: 3rem;
    position: relative;
    h2 {
      font-size: 2.5rem;
      color: $white;
    }
    p {
      font-size: 1.5rem;
      color: $white;
    }
    .col-btn-contacto {
      display: flex;
      align-items: center;
      justify-content: center;
      a {
        font-size: 1.5rem;
        font-weight: 600;
      }
    }
    .btn-compartir {
      position: absolute;
      text-decoration: none;
      top: 0;
      right: 0;
      transform: translateY(-50%);
      .fa-stack {
        font-size: 2.2rem;
        .icon-bg {
          color: $gray-dark;
        }
        .icon-red {
          color: $white;
        }
      }
      &:hover {
        .icon-bg {
          color: $celeste;
          transition: all 0.3s;
        }
        .icon-red {
          color: $white;
          transition: all 0.3s;
        }
      }
    }
  }
}