.section-welcome {
  width: 100%;
  height: 60vh;
  padding-bottom: 5vh;
  position: relative;
  @media (min-width: 768px) {
    height: 70vh;
  }
  .content-bg-welcome {
    width: 100%;
    height: 100%;
    background-image: url(../images/bg-welcome.png);
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .content-img-perfil {
    width: 35vh;
    height: 35vh;
    border-radius: 50%;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -10px);
    background-color: $white;
    box-shadow: 0px 0px 10px $text-gray;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border-radius: 50%;
      transform: scale(0.96);
    }
  }
}